
import React, { PureComponent } from 'react';
import {connect} from 'react-redux';
import './pokemon.scss'; 
import introMp3 from './sound/intro.mp3';


// 자기 소개
class Pokemon extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            contentCode: ''
        }
    }
    componentDidMount() {
        document.getElementById('pokemon-intro-sound')
            .volume = this.props.common.sound.volume / 100;
    }
    componentDidUpdate(prevProps, prevState) {
        if (this.props.common.sound.volume !== prevProps.common.sound.volume){
            document.getElementById('pokemon-intro-sound')
                .volume = this.props.common.sound.volume / 100;
        }
    }
    render(){
        return (
            <div className="pop_content">
                <div className="pop_preview">
                    {/* <audio autoPlay="ture" volume={this.props.common.sound.volume / 100}> */}
                    {/* <source src='https://s3-us-west-2.amazonaws.com/s.cdpn.io/142927/intro.mp3' type='audio/mpeg'/> */}
                    <audio autoPlay="ture" id="pokemon-intro-sound">
                        <source src={introMp3} type='audio/mpeg'/>
                </audio>
                    <div className='wrapper'>
                        <div className='composition'>
                            <div className='scene scene--1'>
                                <div className='shooting-star'></div>
                                <div className='gamefreak-logo'></div>
                                <div className='gamefreak'></div>
                                <div className='star-shower-mask'>
                                    <div className='star-shower--light'></div>
                                    <div className='star-shower--dark'></div>
                                </div>
                            </div>
                            <div className='scene scene--2'>
                                <section className='opponent jigglypuff-wrapper'>
                                    <figure className='jigglypuff'></figure>
                                    <figure className='jigglypuff--flinch'></figure>
                                    <figure className='jigglypuff--tackle'></figure>
                                </section>
                                <section className='opponent nidorino-wrapper'>
                                    <figure className='nidorino'></figure>
                                    <figure className='nidorino--flinch'></figure>
                                    <figure className='nidorino--tackle'></figure>
                                </section>
                                <section className='gengar-wrapper'>
                                    <figure className='gengar'></figure>
                                    <figure className='gengar--scratch1'></figure>
                                    <figure className='gengar--scratch2'></figure>
                                </section>
                            </div>
                            <div className='scene scene--3'>
                                <div className='logo-wrapper'>
                                    <figure className='pokemon-logo'></figure>
                                    <span><strong>BACK Version</strong></span>
                                    <span className="start-btn">▶Start</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


function mapStateToProps(state) {
    return state.redux_commonInfo;
}

export default connect(mapStateToProps,null)(Pokemon);

